"use client"

import { useState, useEffect } from "react"
import { Check, ChevronsUpDown, Search, FileText, Activity } from "lucide-react"
import { cn } from "@/lib/utils"
import { Button } from "@/components/ui/button"
import {
  Command,
  CommandEmpty,
  CommandGroup,
  CommandInput,
  CommandItem,
  CommandList,
} from "@/components/ui/command"
import {
  Popover,
  PopoverContent,
  PopoverTrigger,
} from "@/components/ui/popover"
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { patientsApi, type Patient } from "@/lib/api/patients-api"
import { PrivateRoute } from "@/components/auth/private-route"

// Importamos tus componentes existentes
import ClinicalHistoryTab from "@/components/patients/tabs/clinical-history-tab"
import TreatmentSessionsTab from "@/components/patients/tabs/treatment-sessions-tab"

export default function RecordsPage() {
  return (
    <PrivateRoute>
      <RecordsPageContent />
    </PrivateRoute>
  )
}

function RecordsPageContent() {
  const [open, setOpen] = useState(false)
  const [patients, setPatients] = useState<Patient[]>([])
  const [selectedPatientId, setSelectedPatientId] = useState<number | null>(null)
  const [loading, setLoading] = useState(true)

  // 1. Cargar lista de pacientes para el buscador
  useEffect(() => {
    const loadPatients = async () => {
      try {
        const data = await patientsApi.getAll()
        // Ordenamos alfabéticamente
        setPatients(data.sort((a, b) => a.fullName.localeCompare(b.fullName)))
      } catch (error) {
        console.error("Failed to load patients", error)
      } finally {
        setLoading(false)
      }
    }
    loadPatients()
  }, [])

  // Encontrar el objeto paciente seleccionado para mostrar su nombre/info
  const selectedPatient = patients.find((p) => p.id === selectedPatientId)

  return (
    <div className="mx-auto max-w-7xl px-4 py-8 sm:px-6 lg:px-8 space-y-8 animate-in fade-in-50">
      
      {/* Header */}
      <div className="flex flex-col md:flex-row md:items-center justify-between gap-4">
        <div>
          <h1 className="text-3xl font-bold tracking-tight">Clinical Records Hub</h1>
          <p className="text-muted-foreground">
            Quickly search and view history and sessions without navigating to profiles.
          </p>
        </div>
      </div>

      {/* Buscador de Pacientes (Combobox) */}
      <Card className="border-l-4 border-l-primary">
        <CardContent className="pt-6">
            <div className="flex flex-col space-y-2">
              <label className="text-sm font-medium leading-none peer-disabled:cursor-not-allowed peer-disabled:opacity-70">
                Search Patient to View Records
              </label>
              <Popover open={open} onOpenChange={setOpen}>
                <PopoverTrigger asChild>
                  <Button
                    variant="outline"
                    role="combobox"
                    aria-expanded={open}
                    className="w-full md:w-100 justify-between"
                  >
                    {selectedPatient
                      ? selectedPatient.fullName
                      : "Select a patient..."}
                    <ChevronsUpDown className="ml-2 h-4 w-4 shrink-0 opacity-50" />
                  </Button>
                </PopoverTrigger>
                <PopoverContent className="w-100 p-0">
                  <Command>
                    <CommandInput placeholder="Search patient..." />
                    <CommandList>
                      <CommandEmpty>No patient found.</CommandEmpty>
                      <CommandGroup>
                        {patients.map((patient) => (
                          <CommandItem
                            key={patient.id}
                            value={patient.fullName}
                            onSelect={() => {
                              setSelectedPatientId(patient.id)
                              setOpen(false)
                            }}
                          >
                            <Check
                              className={cn(
                                "mr-2 h-4 w-4",
                                selectedPatientId === patient.id ? "opacity-100" : "opacity-0"
                              )}
                            />
                            <div className="flex flex-col">
                                <span>{patient.fullName}</span>
                                <span className="text-xs text-muted-foreground">{patient.email}</span>
                            </div>
                          </CommandItem>
                        ))}
                      </CommandGroup>
                    </CommandList>
                  </Command>
                </PopoverContent>
              </Popover>
            </div>
        </CardContent>
      </Card>

      {/* Área de Visualización */}
      {selectedPatientId ? (
        <div className="space-y-6 animate-in slide-in-from-bottom-5 duration-500">
          
          {/* Info Resumida del Paciente */}
          <div className="flex items-center gap-4 p-4 bg-muted/30 rounded-lg border">
            <div className="h-12 w-12 rounded-full bg-primary/10 flex items-center justify-center text-primary font-bold text-lg">
                {selectedPatient?.fullName.charAt(0)}
            </div>
            <div>
                <h2 className="text-xl font-bold">{selectedPatient?.fullName}</h2>
                <p className="text-sm text-muted-foreground">
                    Record ID: #{selectedPatient?.id} • {selectedPatient?.email}
                </p>
            </div>
          </div>

          {/* Tabs Reutilizadas */}
          <Tabs defaultValue="history" className="w-full">
            <TabsList className="grid w-full grid-cols-2 lg:w-100">
              <TabsTrigger value="history" className="flex gap-2">
                <FileText className="w-4 h-4" /> Clinical History
              </TabsTrigger>
              <TabsTrigger value="sessions" className="flex gap-2">
                <Activity className="w-4 h-4" /> Treatment Sessions
              </TabsTrigger>
            </TabsList>
            
            <div className="mt-6">
                <TabsContent value="history">
                    <ClinicalHistoryTab patientId={selectedPatientId} />
                </TabsContent>
                <TabsContent value="sessions">
                    <TreatmentSessionsTab patientId={selectedPatientId} />
                </TabsContent>
            </div>
          </Tabs>

        </div>
      ) : (
        // Estado vacío (Empty State)
        <div className="flex flex-col items-center justify-center py-16 text-center text-muted-foreground opacity-50">
            <Search className="h-16 w-16 mb-4 stroke-1" />
            <h3 className="text-lg font-medium">No patient selected</h3>
            <p>Use the search bar above to find clinical records.</p>
        </div>
      )}
    </div>
  )
}